.search-bar {
  /* width: 50vw; */
  height: 41px;
  max-width: 1120px;
  max-height: 41px;
  background-color: #f3f3f3;
  border-bottom: 1px solid var(--light-gray);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  /* margin-left: 7.8vw;
  margin-right: 7.8vw; */
}

.search-bar-mobile {
  width: 80vw;
  height: 41px;
  /* max-width: 1120px; */
  /* max-height: 41px; */
  background-color: #f3f3f3;
  border-bottom: 1px solid var(--light-gray);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  /* margin-left: 7.8vw;
  margin-right: 7.8vw; */
}

.search-bar-mobile .search-bar-input {
  width: auto;
  width: 60vw;
  background: none;
  border: none;
  outline: none;
}

.search-bar-mobile .btn-search {
  margin-left: 15px;
}

.search-bar-input {
  width: auto;
  width: 100vw;
  background: none;
  border: none;
  outline: none;
}

.search-menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-category {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--gray);
}

.search-category p {
  white-space: nowrap;
  margin-left: 10px;
  margin-right: 15px;
}

.btn-search {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.btn-search i {
  color: var(--gray);
}

@media screen and (max-width: 1650px) {
  .search-bar {
    width: 50vw;
  }
}

@media screen and (max-width: 1100px) {
  .search-bar {
    width: 45vw;
  }
}

@media screen and (max-width: 1000px) {
  .search-bar {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .search-bar-mobile .search-bar-input {
    width: auto;
    width: 40vw;
    background: none;
    border: none;
    outline: none;
  }

  .search-bar-mobile {
    padding-left: 10px;
    padding-right: 10px;
  }

  .search-bar-mobile .btn-search {
    margin-left: 10px;
  }
}
