.home-banner {
  min-height: 460px;
  background: linear-gradient(89.92deg, #000000 0.06%, #31353c 99.93%);
  background: linear-gradient(89.92deg, #000000 0%, #31353c 100%);
  mix-blend-mode: normal;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  color: white;
}

.home-banner .text-container {
  display: flex;
  flex-direction: column;
  flex: 0.5;
  justify-content: center;
  align-items: center;
}

.home-banner h1 {
  font-size: 48px;
  font-weight: bold;
}

.home-banner h2 {
  font-size: 22px;
}

.home-banner p {
  margin-top: 50px;
  /* font-size: 18px; */
}

.home-banner .img-container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex: 0.5;
}

.shop-now-btn {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border: 2px solid white;
  width: 130px;
  height: 36px;
}

.shop-now-btn:hover,
.shop-now-btn:active,
.shop-now-btn:focus {
  color: black !important;
}

.shop-now-btn i {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  font-size: 20px;
}

@media screen and (max-width: 1280px) {
  .home-banner .img-container img {
    width: 400px;
  }

  .home-banner h1 {
    font-size: 40px;
    font-weight: bold;
  }

  .home-banner h2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 1050px) {
  .home-banner .text-container {
    margin-left: 25px;
  }
}

@media screen and (max-width: 530px) {
  .home-banner {
    flex-direction: column;
  }

  .home-banner .text-container {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .home-banner .img-container img {
    width: 50vw;
  }
}
