* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --accent-color-1: #0072bc;
  --accent-color-2: #005a96;

  --text-color-1: #0e1011;
  --text-color-2: #16191b;

  --light-color-1: #e9ecef;
  --light-color-2: #d7dadf;
  --light-color-3: #7c8791;

  --danger-color: #db1f48;

  --green-color: #00735c;
  --background-color: #f8f9fa;

  --gray: #4b4b4b;
  --light-gray: #cbcbcb;
  --light-gray-2: #f0f0f0;
  --gray-text: #616161;

  --button-text-color: var(--background-color);

  --transition-delay: 0.125s;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif !important;
  font-weight: 400;
  background-color: var(--background-color);
}

.container-ns {
  margin-left: 25px;
  margin-right: 25px;
}

@media screen and (max-width: 450px) {
  .container-ns {
    margin-left: 15px;
    margin-right: 15px;
  }
}

p {
  margin-bottom: 0 !important;
}

a {
  text-decoration: none !important;
  /* color: inherit !important; */
  /* all: unset; */
  cursor: pointer;
}

.line-through {
  text-decoration: line-through;
}

.bold {
  font-weight: bold;
}

.small-text {
  font-size: 16px;
}

.smaller-text {
  font-size: 14px;
}

.small-title {
  font-size: 26px;
}

.btn {
  padding: 0 !important;
}

.transparent-btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.transparent-btn:hover,
.transparent-btn:active,
.transparent-btn:focus {
  background-color: transparent !important;
}

.custom-button {
  /* Reset btn style */
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
  /* Custom btn style */
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1.2px solid #6c757d;
  color: var(--gray);
  border-radius: 2px;
}

.custom-button:hover {
  /* Reset btn style */
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
  /* Custom btn style */
  border: 1.2px solid #6c757d;
  background-color: var(--light-gray-2);
}

.custom-button:active {
  /* Reset btn style */
  outline: none;
  border: none;
  /* Custom btn style */
  border: 1.2px solid #6c757d;
  background-color: var(--light-gray-2);
}

.custom-button:focus {
  /* Reset btn style */
  outline: 0;
  /* Custom btn style */
  border: 1.2px solid #6c757d;
  background-color: var(--light-gray-2);
}

@media screen and (max-width: 1280px) {
  .small-text {
    font-size: 14px;
  }

  .small-title {
    font-size: 22px;
  }

  .smaller-text {
    font-size: 10px;
  }
}

