.home-footer {
  margin-top: 100px;
  border-top: 1px solid var(--gray-text);
}

.footer-links-container {
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 50px;
  margin-bottom: 75px;
}

.home-footer .footer-links-container {
  display: flex;
  justify-content: space-between;
}

.home-footer .footer-links-container h4 {
  margin-bottom: 10px;
}

.home-footer .footer-links-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.home-footer .footer-links-container a {
  color: var(--gray-text);
  margin-top: 10px;
}

@media screen and (max-width: 1000px) {
  .home-footer .footer-links-container {
    margin-left: 50px;
    margin-right: 50px;
  }
}

@media screen and (max-width: 600px) {
  .home-footer .footer-links-container {
    flex-direction: column;
  }
  .home-footer .footer-links {
    margin-bottom: 25px;
  }
}
